<template>

	<div>
		<v-btn fab color="red accent-1" right absolute @click="add()">
			<v-icon>mdi-plus</v-icon>
		</v-btn>
		<v-card-title>
			<span class="headline">Clientes</span>
		</v-card-title>

		<v-data-table v-bind:headers="headers" :items="customers" class="elevation-1"
			:header-props="{ sortByText: 'Ordenar por' }">
			<template slot="items" slot-scope="props">
				<td>{{ props.item.name }}</td>
				<td class="text-xs-right">{{ props.item.phone }}</td>
				<td class="text-xs-right">{{ props.item.email }}</td>

			</template>

			<template v-slot:item.actions="{ item }">

				<v-icon  @click="edit(item)" class="mr-2">
					mdi-pencil
				</v-icon>
				<router-link :to="{ name: 'CustomerOrders', params: { customerId: item.id } }">
					<v-icon  class="mr-2">
						mdi-handshake
					</v-icon>
				</router-link>

				<v-icon >
					mdi-delete
				</v-icon>

			</template>
		</v-data-table>

		<customer v-bind:customer="item" v-bind:activate.sync="showCustomer"></customer>

	</div>

</template>

<script>

import firebase from "firebase";
import customer from "./Customer.vue"
//import order from "./Order.vue"

export default {

	name: "Customer",
	components: {
		customer
	},
	data() {
		return {
			showCustomer: false,
			headers: [
				{ text: 'Nome', value: 'name', align: 'left' },
				{ text: 'Telefone', value: 'phone', align: 'left' },
				{ text: 'E-mail', value: 'email', align: 'left' },
				{ text: 'Ações', value: 'actions', sortable: false },
			],
			customers: [],
			item: {}
		};
	},

	created() {

		this.loadCustomers();

	},


	methods: {

		loadCustomers() {

			var query = firebase.firestore().collection("customer");

			query.onSnapshot(snap => {
				this.customers = [];
				snap.forEach(doc => {
					let cust = doc.data();
					cust.id = doc.id;

					this.customers.push(cust);
				});
			});

		},

		edit(item) {

			this.item = item;
			this.showCustomer = true;

		},
		add() {
			this.item = new Object();
			this.showCustomer = true;
		}

	}

}

</script>